const obfuscateAccountNumber = (accNo) => `****${accNo.substring(4)}`;

const removeTimeFromStringDate =
  date => (date && date.length >= 10 ? date.substring(0, 10) : date);

const getPaymentDetails = ({
  payeeAccountNumber,
  payeeName,
  payeeNickName,
  paymentAccountNumber,
  paymentAccountNickName = '',
}) => {
  const payeeAccount = payeeAccountNumber ? ` (${obfuscateAccountNumber(payeeAccountNumber)})` : '';
  const payee = `${payeeNickName || payeeName}${payeeAccount}`;

  const obfuscatedAccNo = obfuscateAccountNumber(paymentAccountNumber);
  const formattedPaymentAccNo = paymentAccountNickName ? ` (${obfuscatedAccNo})` : obfuscatedAccNo;
  const paymentAccount = `${paymentAccountNickName}${formattedPaymentAccNo}`;

  return { payee, paymentAccount };
};

/**
 * @name Hooks
 * @type {Object}
 * @description Hooks for widget-bb-billpay-view-payments-ng
 */
export default {
  /**
   * @name Hooks#transformPayment
   * @type {function}
   * @description Perform any transformations required to prepare the Payment item for display
   * @param {module:model-bb-billpay-ng.Payment} payment
   * @return {Object} transformed payment
   */
  transformPayment(payment) {
    const { recurring } = payment;
    return {
      ...payment,
      // remove time from server date so that there is no TZ conversion
      paymentDate: removeTimeFromStringDate(payment.paymentDate),
      paymentType: recurring ? 'payment.payment-type.recurring' : 'payment.payment-type.one-time',
      paymentDetails: getPaymentDetails(payment),
    };
  },
};



